html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  font-family: "SFProDisplay", sans-serif;
  background-color: var(--color-white);
  display: flex;
  flex-direction: column;
}

.main {
  flex: 1 0 auto;
}

.footer {
  flex-shrink: 0;
}

img {
  max-width: 100%;
}

a {
  text-decoration: none;
  color: inherit;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

input,
button,
textarea {
  font-family: inherit;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  font-size: inherit;
  font-weight: inherit;
  margin: 0;
  padding: 0;
}

.is-outer {
  position: relative !important;
  left: -9999px !important;
}

.site-container {
  overflow: hidden;
}

.flex {
  display: flex;
}

.stop-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
}

.is-hidden {
  display: none !important;
}

.for-mobile {
  display: none;
}

@media (max-width: 1024px) {
  .for-mobile {
    display: block;
  }
}

.error {
  border: 2px #ff4019 solid !important;
}

.visually-hidden {
  position: absolute !important;
  clip: rect(1px 1px 1px 1px);
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0 !important;
  border: 0 !important;
  height: 1px !important;
  width: 1px !important;
  overflow: hidden;
}

.btn-reset {
  border: none;
  background: transparent;
  cursor: pointer;
}

.btn {
  font-size: 12px;
  letter-spacing: 2.18182px;
  font-weight: 700;
  padding: 33px 80px;
  color: var(--color-white);
  border-radius: 8px;
  text-transform: uppercase;
}

.container {
  margin: 0 auto;
  padding: 0 30px;
  max-width: 1626px;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.section-title {
  font-size: 64px;
  line-height: 80px;
  font-weight: 700;
  color: var(--color-dark);
}

.section-title--center {
  text-align: center;
  max-width: none;
}

.header {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  z-index: 100;
}

.header__container {
  justify-content: space-between;
  padding-top: 55px;
  padding-bottom: 55px;
  align-items: center;
}

.social {
  display: flex;
  align-items: center;
}

.social__item:not(:last-child) {
  margin-right: 40px;
}

.social__link {
  display: block;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.social__link--fb {
  background-image: url(../../img/fb.svg);
  width: 11px;
  height: 20px;
}

.social__link--tw {
  background-image: url(../../img/tw.svg);
  width: 20px;
  height: 17px;
}

.logo {
  font-size: 22px;
  line-height: 1;
  text-align: center;
  letter-spacing: 2px;
  font-weight: 700;
  color: var(--color-white);
  text-transform: uppercase;
}

.first {
  height: 100vh;
  min-height: 660px;
  background-image: url(../../img/first-bg.jpg);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  color: var(--color-white);
}

.first__container {
  position: relative;
  height: 100%;
  padding-top: 223px;
  padding-bottom: 223px;
}

@media (max-height: 1079px) {
  .first__container {
    padding: 120px 30px;
  }
}

@media (max-height: 880px) {
  .first__container {
    padding: 100px 30px;
  }
}

.first_descr {
  font-family: "SFCompactText", sans-serif;
  font-size: 14px;
  line-height: 16px;
  font-weight: 400;
  max-width: 32%;
  margin-bottom: 60px;
}

@media (max-height: 780px) {
  .first_descr {
    font-size: 12px;
    margin-bottom: 30px;
  }
}

.first__link {
  background-color: rgba(28, 46, 61, 0.4);
  font-size: 11px;
  letter-spacing: 2px;
  font-weight: 700;
  padding: 15px 30px;
  border-radius: 100px;
  text-transform: uppercase;
  display: inline-block;
  margin-bottom: 42px;
}

@media (max-height: 880px) {
  .first__link {
    margin-bottom: 30px;
  }
}

.first__title {
  font-size: 80px;
  line-height: 84px;
  font-weight: 700;
  max-width: 40%;
  margin-bottom: 40px;
}

@media (max-height: 880px) {
  .first__title {
    font-size: 75px;
    line-height: 65px;
  }
}

@media (max-height: 780px) {
  .first__title {
    font-size: 60px;
    line-height: 65px;
    margin-bottom: 20px;
  }
}

.first__btn {
  background-color: var(--color-accent);
  position: relative;
  z-index: 5;
}

@media (max-height: 780px) {
  .first__btn {
    padding: 25px 40px;
  }
}

.first__bottom {
  position: absolute;
  left: 50%;
  bottom: 0;
  transform: translateX(-50%);
  width: calc(100% - 60px);
  display: flex;
  align-items: flex-end;
}

.search-form {
  flex-shrink: 0;
  width: 68%;
  position: relative;
  overflow: hidden;
  border-top-left-radius: 4px;
  height: 120px;
}

@media (max-height: 780px) {
  .search-form {
    height: 80px;
  }
}

.search-form__field {
  border: none;
  background-color: #fff;
  width: 100%;
  height: 100%;
  color: var(--color-dark);
  padding-left: 55px;
  padding-right: 90px;
  border-bottom: 1px solid rgba(223, 223, 223, 0.5);
}

.search-form__button {
  width: 18px;
  height: 18px;
  background-image: url(../../img/search.svg);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  position: absolute;
  right: 62px;
  top: 50%;
  transform: translateY(-50%);
}

.main-slider {
  overflow: hidden;
  border-top-left-radius: 8px;
  border-top-right-radius: 8px;
  font-size: 0;
}

.main-slider__text {
  height: 120px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: var(--color-neptune);
  text-transform: uppercase;
  color: var(--color-white);
  font-size: 12px;
  letter-spacing: 2.18182px;
}

@media (max-height: 780px) {
  .main-slider__text {
    height: 80px;
  }
}

.become {
  background-color: #fff;
  padding-top: 150px;
  padding-bottom: 120px;
}

.become__title {
  max-width: 50%;
}

.become__list {
  display: flex;
  flex-wrap: wrap;
  margin-top: 95px;
}

.become__item {
  width: 23.5%;
  margin-right: 2%;
  margin-bottom: 2%;
  overflow: hidden;
  border-radius: 4px;
  position: relative;
  font-size: 0;
}

.become__item::before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  background: var(--color-neptune);
  opacity: 0;
  transition: opacity 0.3s ease-in-out;
}

.become__item:hover::before {
  opacity: 0.9;
  transition: opacity 0.3s ease-in-out;
}

.become__item:hover .become__text {
  transform: translateY(0);
  transition: transform 0.3s ease-in-out;
}

.become__item:nth-child(4n) {
  margin-right: 0;
}

.become__image {
  object-fit: cover;
}

.become__text {
  max-width: 230px;
  position: absolute;
  left: 0;
  bottom: 0;
  padding-left: 54px;
  padding-bottom: 64px;
  font-size: 24px;
  line-height: 31px;
  font-weight: 700;
  color: var(--color-white);
  transform: translateY(300px);
  transition: transform 0.3s ease-in-out;
}

.featured {
  background-image: linear-gradient(180deg, rgba(28, 46, 59, 0.0001) 0%, #091117 100%), url(../../img/featured-bg.jpg);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  height: 1080px;
  position: relative;
}

.featured__container {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
}

.featured__stories {
  position: absolute;
  right: 0;
  top: 0;
  height: 100%;
  width: 35.2%;
  background-color: rgba(60, 74, 81, 0.9);
  padding: 126px 132px;
}

.featured__title {
  font-size: 64px;
  line-height: 80px;
  font-weight: 700;
  color: #fff;
  margin-bottom: 65px;
}

.blog-article {
  padding-bottom: 105px;
}

.blog-article__category {
  display: inline-block;
  font-size: 11px;
  line-height: 1;
  text-align: center;
  letter-spacing: 2px;
  font-weight: 700;
  padding: 15px 31px;
  color: var(--color-white);
  background-color: var(--color-accent);
  border-radius: 100px;
  text-transform: uppercase;
  margin-bottom: 20px;
}

.blog-article__title {
  font-size: 48px;
  line-height: 64px;
  font-weight: 700;
  color: var(--color-white);
  max-width: 40%;
  margin-bottom: 18px;
}

.blog-article__descr {
  font-family: "SFCompactText", sans-serif;
  font-size: 14px;
  line-height: 21px;
  font-weight: 300;
  color: var(--color-white);
  margin-bottom: 40px;
  max-width: 40%;
}

.blog-article__link {
  display: inline-block;
  text-align: center;
  background-color: var(--color-neptune);
}

.feature-list__article {
  border-bottom: 1px solid #4d5a5f;
  display: flex;
  align-items: flex-start;
  padding: 32px 0;
}

.feature-list__item:last-child .feature-list__article {
  border-bottom: 1px solid transparent;
}

.feature-list__image {
  border-radius: 100%;
  overflow: hidden;
  width: 48px;
  height: 48px;
  flex-shrink: 0;
}

.feature-list__image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.feature-list__text {
  margin-left: 32px;
  padding-right: 35px;
}

.feature-list__title {
  font-size: 18px;
  line-height: 24px;
  font-weight: 700;
  color: #fff;
  margin-bottom: 5px;
}

.feature-list__time {
  font-family: "SFProText", sans-serif;
  font-size: 11px;
  font-weight: 400;
  color: rgba(255, 255, 255, 0.502293);
}

.burger {
  --burger-width: 48px;
  --burger-height: 10px;
  --burger-line-height: 2px;
  width: var(--burger-width);
  height: var(--burger-height);
  position: relative;
  color: var(--color-white);
}

.burger__line {
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  width: 100%;
  height: var(--burger-line-height);
  background-color: currentColor;
  transition: transform 0.3s ease-in-out;
}

.burger::before, .burger::after {
  content: "";
  position: absolute;
  left: 0;
  width: 100%;
  height: var(--burger-line-height);
  background-color: currentColor;
  transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
}

.burger::before {
  top: 0;
}

.burger::after {
  top: calc(100% - var(--burger-line-height));
}

.burger--active .burger__line {
  transform: scale(0);
  transition: transform 0.3s ease-in-out;
}

.burger--active::before {
  transform: rotate(45deg);
  top: 50%;
  transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
}

.burger--active::after {
  transform: rotate(-45deg);
  top: 50%;
  transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
}
